h1 {
    margin-top: 15px;
    margin-left: 20px;
}

.list {
    margin-top: 10px;
    margin-left: 15px;
    font-size: 16px;
    font-weight: bold;
}

.list {
    margin-top: 10px;
    margin-left: 15px;
    font-size: 16px;
    font-weight: bold;
}

.main {
    padding: 10px;
}

.title {
    text-align: center;
    color: rgb(255, 255, 255);
}

.moviesList {
    display: grid;
    max-width: calc(100vw - 35px);
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 16px;
    margin: 10px auto 0px;
    padding: 0px 0px 30px;
    list-style: none;
}

.moviesItem {
    border-radius: 5px;
    box-shadow: inset rgba(0, 0, 0, 0.5) -3px -3px 8px,
        inset rgba(255, 255, 255, 0.9) 3px 3px 8px,
        rgba(0, 0, 0, 0.8) 3px 3px 8px -3px;
    overflow: hidden;
}

.poster {
    width: 100%;
    min-height: 410px;
    object-fit: cover;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.poster:hover {
    transform: scale(1.03);
    cursor: pointer;
}

.movieTitle {
    display: flex;
    justify-content: center;
    padding-top: 18px;
    padding-bottom: 22px;
    align-items: center;
    text-align: center;
    font-style: italic;
    color: white;
}

.link {
    text-decoration: none;
}